<template>
    <div class="mechanism">
        <div class="mechTop">
            <div class="topTitle">
                <p>服务机构</p>
            </div>
        </div>
        <div class="mechContent">
            <el-container>
                <el-aside style="font-size: 14px;" width="200px">
                    <div class="treeTitle"><span>全部</span></div>
                    <div class="treeData">
                        <el-tree
                            :props="props"
                            ref="tree"
                            lazy
                            :load="loadNode"
                            node-key="id"
                            :expand-on-click-node="false"
                            @node-click="nodeClick">
                            <span class="tree-node" slot-scope="{ node, data }" :title="data.name">
                                <span>{{data.name}}</span>
                            </span>
                        </el-tree>
                    </div>
                </el-aside>
                <el-container>
                    <el-header style="text-align: center;height: 40px">
                        <div class="yijiTitle">调解机构</div>
                    </el-header>
                    <el-main>
                        <div>
                            <el-input
                                placeholder="搜索"
                                v-model="input4">
                                <i slot="prefix" class="el-input__icon el-icon-search"></i>
                            </el-input><el-button type="primary" icon="el-icon-search">搜索</el-button>
                        </div>
                        <div class="checkBox">
                            <el-checkbox-group v-model="checkedCities" @change="getcheck()">
                                <el-checkbox v-for="(item, index) in cities" :label="item.id" :key="index">{{item.name}}</el-checkbox>
                            </el-checkbox-group>
                        </div>
                        <div class="listBox" v-show="listBx">
                            <div class="listTop" v-for="(item, index) in listData" :key="index">
                              <div class="listImg">
                                  <img :src="touXiang" alt="">
                              </div>
                              <div class="listContent">
                                  <h1>{{item.name}}</h1>
                                  <p>{{item.enname}}</p>
                                  <p><span>类型：{{item.enname}}</span><span>类型：{{item.enname}}</span></p>
                                  <p><span>类型：{{item.enname}}</span><span>类型：{{item.enname}}</span></p>
                              </div>
                              <div class="details">
                                  <el-button type="primary" plain  @click="getDetail(item.id)">详情</el-button>
                              </div>
                            </div>
                        </div>
                        <div class="pagination">
                            <el-pagination
                                background
                                :current-page="currentPage4"
                                :page-sizes="pageSizes"
                                :page-size="pageSize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="total">
                            </el-pagination>
                        </div>
                    </el-main>
                </el-container>
            </el-container>
        </div>
        <el-dialog
            title="服务人员详情"
            :visible.sync="dialogVisible"
            width="30%">
            <div class="listBox">
                <div class="listTop dialogBox">
                    <div class="listImg">
                        <img :src="touXiang" alt="">
                    </div>
                    <div class="listContent detailContent">
                        <h1>{{listData[0].title}}</h1>
                        <p>{{listData[0].place}}</p>
                        <p><span>类型：{{listData[0].type}}</span><span>类型：{{listData[0].type}}</span></p>
                        <p><span>类型：{{listData[0].type}}</span><span>类型：{{listData[0].type}}</span></p>
                    </div>
                </div>
                <div class="rate">
                    <span>调解能力：</span><el-rate v-model="value" :colors="colors" disabled></el-rate>
                    <span>调解能力：</span><el-rate v-model="value2" :colors="colors" disabled></el-rate>       
                    <span>调解能力：</span><el-rate v-model="value3" :colors="colors" disabled></el-rate>  
                </div>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {getRole,getOffice,getParent,getRoleId} from '@/request'
export default {
    data() {
        return{
            data: [],
            props: {
                label: 'name',
                children: 'zones',
                isLeaf: 'leaf'
            },
            input4: '',
            checkedCities: [],
            cities: [],
            listData: [
                // {imgUrl: ,title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                // {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                // {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                // {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                // {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                // {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                // {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                // {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
                // {imgUrl: require('../assets/serviceUser.png'),title: '人情味若群无',place: '地点地点地点地点',type: '驱蚊器翁群翁群无'},
            ],
            touXiang: require('../assets/serviceUser.png'),
            listBx: false,
            currentPage4: 1,
            pageSizes: [10, 20, 30, 100],
            pageSize: 10,
            total: 6,
            dialogVisible: false,
            value: 3.4,
            value2: 2.1,
            value3: 4.6,
            colors: ['#99A9BF', '#F7BA2A', '#FF9900']
        }
    },
    methods: {
        // shu
         handleNodeClick(data) {
            console.log(data);
        },
        loadNode(node, resolve) {
            if (node.level === 0) {
                this.getTree(resolve)
            } else {
                this.getTreeChild(node.data.id, resolve)
            }
        },
        getTreeChild(id, resolve) {
            getParent({id}).then((res) => {
                this.cities = res.data
                return resolve(res.data);
            })
        },
        nodeClick(node) {
            var id = node.id
            getParent({id}).then((res) => {
                this.cities = res.data
            })
        },
        getTree (resolve) {
            getOffice({"parentId":0,"officeType":1,"type":1}).then((res) => {
                return resolve(res.data);
            })
        },
        // 工作站
        getcheck() {
            getRole(this.checkedCities).then((res) => {
                this.listData = res.data
                if (this.listData == []) {
                    this.listBx = false
                } else {
                    this.listBx = true
                }
            })
        },
        getDetail(id) {
            this.dialogVisible = true
            getRoleId(id).then((res) => {
                
            })
        }
    },
    mounted() {
        this.getTree()
    }
}
</script>>
<style scoped>
.mechTop{
    width: 100%;
    height: 315px;
    background-image: url(../assets/mechanism.png);
    background-repeat:no-repeat;
    background-size: cover;
    position: relative;
}
.topTitle{
    height: 90px;
    background-color: #fff;
    margin: 0 10%;
    position: absolute;
    bottom: 0;
    left: 10%;
    right: 10%;
    border: 1px solid rgb(227, 227, 227);
}
.topTitle p{
    text-align: left;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 20px;
    color: rgb(16, 16, 16);
    margin: 0 35px;
    line-height: 90px
}
.mechContent{
    padding: 30px 10%;
}
.treeTitle{
    border-left: 2px solid rgb(13, 114, 183);
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 14px;
    color: rgb(16, 16, 16);
    font-style: normal;
    letter-spacing: 0px;
    line-height: 20px;
    padding-left: 20px;
    text-align: left
}
.el-input{
    width: 40%;
    padding-right: 20px;
}
.checkBox{
    text-align: left;
    margin-top: 15px
}
.listTop{
    width: 100%;
    height: 125px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    margin-top: 30px;
}
.listTop:hover{
    box-shadow: rgba(0, 0, 0, 0.11) 6px 1px 7px 7px
}
.listImg{
    width: 85px;
    height: 85px;
    border-radius: 50%;
    float: left;
    margin-right: 15px
}
.listImg img{
    width: 100%
}
.listContent{
    width: 75%;
    float: left;
    font-family: SourceHanSansSC;
    font-weight: 400;
    line-height: 20px;
    text-align: left
}
.listContent h1{
    margin: 0 0 10px 0;
    font-size: 14px;
    color: rgb(16, 16, 16);
}
.listContent p{
    margin: 0;
    font-size: 12px;
    color: rgba(154, 152, 152, 1);
}
.details{
    float: right
}
.pagination{
    margin-top: 30px
}
.yijiTitle{
    height: 40px;
    background-color: rgb(241, 241, 241);
    border-left: 3px solid rgb(13, 114, 183);
    text-align: left;
    padding-left: 15px;
    line-height: 40px;
    font-weight: 400;
    font-size: 16px;
    color: rgb(16, 16, 16);
}
.dialogBox{
    border: none;
    margin-top: 0px;
    box-shadow: none
}
.dialogBox:hover{
    box-shadow: none
}
.detailContent{
    width: 100%
}
.rate{
    text-align: left;
    padding-left: 80px
}
.rate span{
    float: left;
}
</style>
<style>
.el-tree{
    margin-top: 15px;
    border-top: 1px solid #ccc
}
.el-tree-node__content{
    height: 45px !important;
    border-bottom: 1px solid #ccc
}
.el-tree-node:focus>.el-tree-node__content{
    background-color: rgb(13, 114, 183) !important;
    color: #fff
}
.el-tree-node__content:hover{
    background-color: rgb(13, 114, 183) !important;
    color: #fff
}
.el-dialog__title{
    border-left: 3px solid #0d72b7;
    height: 30px;
    display: block;
    padding-left: 10px;
    line-height: 30px
}
.el-dialog__header{
    border-bottom: 1px solid #ccc;
    text-align: left;
}
</style>


